import { SizeUnit, TdUploadFile } from './types';

/**
 * [*] Ë°®Á§∫ÊñπÊ≥ïÈááÁî®ËøôÁßçÊñπÂºè
 * [x] Ë°®Á§∫ÊñπÊ≥ï‰∏çÈááÁî®ËøôÁßçÊñπÂºè
 *
 * [x] bit      ‰Ωç              b     0 or 1
 * [*] byte     Â≠óËäÇ            B     8 bits
 * [x] kilobit  ÂçÉ‰Ωç            kb    1000 bites
 * [*] kilobyte ÂçÉÂ≠óËäÇ(‰∫åËøõÂà∂)   KB    1024 bytes
 * [x] kilobyte ÂçÉÂ≠óËäÇ(ÂçÅËøõÂà∂)   KB    1000 bytes
 * [x] Megabite Áôæ‰∏á‰Ωç          Mb    1000 kilobits
 * [*] Megabyte ÂÖÜÂ≠óËäÇ(‰∫åËøõÂà∂)   KB    1024 kilobytes
 * [*] Megabyte ÂÖÜÂ≠óËäÇ(ÂçÅËøõÂà∂)   KB    1000 kilobytes
 * [x] Gigabit  ‰∏á‰∫ø‰Ωç          Gb    1000 Megabite
 * [*] Gigabyte ÂêâÂ≠óËäÇ(‰∫åËøõÂà∂)   GB    1024 Megabytes
 * [x] Gigabyte ÂêâÂ≠óËäÇ(ÂçÅËøõÂà∂)   GB    1000 Megabytes
 */
export const SIZE_MAP = {
  B: 1024,
  KB: 1,
  MB: 1048576, // 1024 * 1024
  GB: 1073741824, // 1024 * 1024 * 1024
};

export function returnFileSize(number: number) {
  if (number < SIZE_MAP.B) {
    return `${number} Bytes`;
  }
  if (number >= SIZE_MAP.B && number < SIZE_MAP.MB) {
    return `${(number / SIZE_MAP.B).toFixed(1)} KB`;
  }
  if (number >= SIZE_MAP.MB) {
    return `${(number / SIZE_MAP.MB).toFixed(1)} MB`;
  }
  return '';
}

export function getCurrentDate() {
  const d = new Date();
  let month: string | number = d.getMonth() + 1;
  month = month < 10 ? `0${month}` : month;
  return `${d.getFullYear()}-${month}-${d.getDate()} ${d.getHours()}:${d.getMinutes()}:${d.getSeconds()}`;
}

/**
 * Áº©Áï•Êñá‰ª∂Âêç ABCDEFG => ABC...FG
 * @param inputName Êñá‰ª∂Âêç
 * @param leftCount Â∑¶ËæπÈïøÂ∫¶
 * @param rightCount Âè≥ËæπÈïøÂ∫¶
 * @returns Áº©Áï•ÂêéÁöÑÊñá‰ª∂Âêç
 */
export function abridgeName(
  inputName: string,
  leftCount = 5,
  rightCount = 7
): string {
  const name = inputName;
  let leftLength = 0;
  let rightLength = 0;
  for (let i = 0; i < name.length; i++) {
    const w = name[i];
    const isCn = escape(w).indexOf('%u') === 0;
    if (i < leftCount * 2 && leftLength < leftCount) {
      // eslint-disable-next-line no-unused-expressions
      isCn ? (leftLength += 1) : (leftLength += 2);
    } else if (i > i - rightCount && rightLength < rightCount) {
      // eslint-disable-next-line no-unused-expressions
      isCn ? (rightLength += 1) : (rightLength += 2);
    }
  }
  return name.replace(
    new RegExp(`^(.{${leftLength}})(.+)(.{${rightLength}})$`),
    '$1‚Ä¶$3'
  );
}

/**
 * Â§ßÂ∞èÊØîËæÉ
 * @param size Êñá‰ª∂Â§ßÂ∞è
 * @param unit ËÆ°ÁÆóÊú∫ËÆ°ÈáèÂçï‰Ωç
 */
export function isOverSizeLimit(
  fileSize: number,
  sizeLimit: number,
  unit: SizeUnit
) {
  // ‰ª• KB ‰∏∫Âçï‰ΩçËøõË°åÊØîËæÉ
  const units = ['B', 'KB', 'MB', 'GB'];
  // ÂêÑ‰∏™Âçï‰ΩçÂíå KB ÁöÑÂÖ≥Á≥ª

  const KBIndex = 1;
  let index = units.indexOf(unit);
  if (index === -1) {
    // eslint-disable-next-line no-console
    console.warn(
      `TDesign Upload Warn: \`sizeLimit.unit\` can only be one of ${units.join()}`
    );
    index = KBIndex;
  }
  const num = SIZE_MAP[unit];
  const limit = index < KBIndex ? sizeLimit / num : sizeLimit * num;
  return fileSize <= limit;
}

export const urlCreator = () => window.webkitURL || window.URL;

/**
 *
 * @param files ÂæÖÂ§ÑÁêÜÊñá‰ª∂
 * @param format Ê†ºÂºèÂåñËßÑÂàô
 */
export function formatFiles(
  files: File[] = [],
  format?: (file: File) => TdUploadFile
) {
  return files.map((fileRaw) => {
    const file = typeof format === 'function' ? format(fileRaw) : fileRaw;
    const uploadFile: TdUploadFile = {
      raw: fileRaw,
      lastModified: fileRaw.lastModified,
      name: fileRaw.name,
      size: fileRaw.size,
      type: fileRaw.type,
      percent: 0,
      status: 'waiting',
      ...file,
    };
    uploadFile.url = urlCreator()?.createObjectURL(fileRaw);
    return uploadFile;
  });
}
